.section {
	//border: 1px solid #fff;
	position: relative;
	background-color: #fff;

	header {
		padding: 60px 50px;
		h2 {
			font-size: 40px;
			padding: 0px;
			margin: 0px;
			margin-bottom: 10px;
		}
		.title-bold {
			font-family: MontserratBold;
		}
		p {
			text-transform: uppercase;
			padding: 0px;
			margin: 0px;
			font-size: 18px;
		}

	} // header

	.body {
		position: relative;
		.section-content {
			//border: 1px solid red;
			width: 50%;
			position: absolute;
			top: 50%;
			left: 15%;
			transform: translateY(-50%);

			p {
				font-family: GlacialIndifference;
				color: #fff;
				font-size: 25px;
				text-transform: uppercase;
				letter-spacing: 1px;
				margin-bottom: 0px;
			}

			h1 {
				font-family: MontserratBold;
				font-size: 80px;
				color: #fff;
				margin-bottom: 0px;
				padding-bottom: 0px;
				padding-top: 0px;
			}
		}
	}

	.footer {
		width: 100%;
		background-color: #fff;
		position: relative;
		padding: 125px 50px;

		.row {
			margin: 0px!important;
		}

		.footer-inner {
				.left-footer {
					width: 350px;
					
					position: absolute!important;
					left: 0%;
					top: 50%;
					transform: translateY(-50%);

					h4 {
						margin:0px;
						padding: 0px;
					}
					p {
						font-size: 16px;
						margin-top: 10px;
					}
				}

				.right-footer {
					width: 300px;
					position: absolute!important;
					right: 0px;
					top: 50%;
					transform: translateY(-50%);
					position: relative;
					
				} // .right-section
		} // .footer-inner
	} // .footer
} // .section


.page2, .page3 {
	width: 100%;
	

	
	.body {
		background-size: cover;
		background-position: 50% 50%;
		position: relative;

		.card {
			width: 47%;
			height: 65%;
			background-color: #fff;
			position: absolute;
			right: 0px;
			top: 50%;
			transform: translateY(-50%);
				
			.card-inner {
				position: relative;
				width: 100%;
				height: 100%;

				ul {
					display: inline-block;
					width: 70%;
					position: absolute;
					right: 50%;
					top: 50%;
					transform: translate(50%, -50%);

					li {
						text-align: right;
						color: #000;
						font-size: 20px;
						line-height: 45px;
						letter-spacing: 1px;
					}
				} // ul
			} // .inner-card
		} // .card
	} // .body
} // .page2


.page3 {
	.regular {
		margin-top: 0px;
	}

	.muted {
		margin-bottom: 20px;
	}

	.description {
		margin-bottom: 20px;
	}
}


.page4 {
	background-color: none;
	

	.textBlock-wrapper {
		background-color: none;
		position: absolute;
		top: 10%;
		left: 10%;

		.textBlock {
			background-color: transparent!important;
			width: 65%;
			margin-bottom: 50px;
			
			h1 {
				color: #fff;
				font-family: Montserrat!important;
				font-size: 45px;
			}

			p {
				font-family: GlacialIndifference;
				color: #fff;
				font-size: 30px;
				line-height: 40px;
				font-weight: 400;
			}

			&:last-child {
				margin-bottom: 0px;
			}
		} // .textBlock
	} // .textBlock-wrapper  
} // .page4


.page5 {
	position: relative;
	padding: 10% 10%;
	header {
		padding: 0px;
		margin-bottom: 50px;
	}
	.table-wrapper {
		position: relative;
		//border: 1px solid red;
		//display: inline-block;
		margin-bottom: 50px;
	
		h2 {
			text-transform: uppercase;
			font-size: 22px;
			font-family: GlacialIndifferenceBold;
			letter-spacing: 1px;
		}

		.table-row {
			padding: 0px 15px;
			p {
				font-family: GlacialIndifference;
				font-size: 18px;
				color: #333;
				line-height: 45px;

				&.no-lineheight {
					line-height: 25px;
					padding: 5px 0px;
				}
			}

			&:nth-child(odd) {
				background-color: #eaeaea;
			}
		}
	} // .table-wrapper
} // .page5


.page6 {
	//border: 1px solid red;

	.page-content {
		width: 90%;
		//height: 80%;
		height: auto;
		overflow: hidden;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: #fff;
	
		padding: 10% 5%;

		header {
			padding: 0px;
		}

		.insights {
			//border: 1px solid red;
			height: auto;
			overflow: hidden;

			.left-insight {
				//border: 1px solid red;
				img {
					width: 80%;
					margin: auto;
					//border: 1px solid red;
					padding: 80px 0px;
				}

				
			} // .left-insight

			.right-insight {
				.gender-block {
					margin-bottom: 80px;
					.gender {
						font-family: GlacialIndifference;
						font-weight: bold;
						font-size: 30px;
						letter-spacing: 5px;
						margin: 0px;
						margin-left: 5px;
						padding: 0px;
					}
					.perc {
						font-family: MontserratBold;
						font-weight: bold;
						font-size: 100px;
						color: #f1c834;
						letter-spacing: -1px;
						margin: 0px;
						padding: 0px;
						line-height: 70px;
					}
				} // .gender-block
			} // .right-insight
			.text-content {
					h2 {
						font-family: GlacialIndifferenceBold;
						font-size: 30px;
						margin: 0px;
						padding: 0px;
					}
					p {
						font-family: GlacialIndifference;
						font-size: 25px;
						margin: 0px;
						margin-top: 10px;
						padding: 0px;
					}
				}
		} // .insights

		.features {
			//border: 1px solid red;
			margin: 0px;
			padding: 0px;
			margin: auto;
			display: table;
			margin-top: 100px;

			li {
				//min-width: 100px;
				//min-height: 100px;
				//border: 1px solid red;
				margin-right: 50px;
				position: relative;
				float: left;
                
                p {
                	font-family: GlacialIndifference;
                	font-size: 23px;
                	width: 150px;
                	text-align: center;
                }

                span {
                	//border: 1px solid red;
					height: 100px;
					&:before {
						vertical-align: center;
					}
                }

				&:last-child {
					margin-right: 0px;
				}

				&:nth-child(1) {
					span {
						@include iconfont("home") {
							font-size: 65px;
							color: #666;
						}
						display: table;
						margin: auto;
						margin-bottom: 15px;
					}
				}

				&:nth-child(2) {
					span {

						@include iconfont("design") {
							font-size: 60px;
							color: #666;
						}
						display: table;
						margin: auto;
						margin-bottom: 15px;
					}
				}

				&:nth-child(3) {
					span {
						@include iconfont("bed") {
							font-size: 60px;
							color: #666;
						}
						display: table;
						margin: auto;
						margin-bottom: 15px;
					}
				}

				&:nth-child(4) {
					span {
						@include iconfont("bath") {
							font-size: 70px;
							color: #666;
						}
						display: table;
						margin: auto;
						margin-bottom: 15px;
					}
				}

			} // li
		} // .features
	} // .page-content
} // .page6


