.card-left {
	left: 0px!important;
	min-width: 55%!important;
	height: auto!important;
	padding: 100px 50px;
	

	.description {
		font-family: GlacialIndifference;
		font-size: 25px;
		line-height: 45px;
	}
}

.seperator {
	position: relative;
	height: 30px;
	background-color: #000;
	width: 100%;

	&.no-padding {
		padding-left: -10%!important;
	}
}

.underline {
	display: inline-block;
	padding-bottom: 10px!important;
	position: relative;
	&:after {
		content: "";
		display: block;
		position: absolute;
		bottom: 0px;
		left: 0px;
		height: 3px;
		width: 35%;
		background-color: #ffca22;
	}
}

.underline-white {
	display: inline-block;
	padding-bottom: 15px!important;
	position: relative;
	&:after {
		content: "";
		display: block;
		position: absolute;
		bottom: 0px;
		left: 0px;
		height: 3px;
		width: 40%;
		background-color: #fff;
	}	
}

.regular {
	font-family: Montserrat;
	font-weight: 400;
	font-size: 35px;
}

.muted {
	color: #444;
	font-size: 20px;
}

.pencil {
	@include iconfont("pencil") {
		font-size: 50px;
		color: #000;
	}
}

.bg-white {
	background-color: #fff;
}


