.box-div
{
	background: #e0e3e6;
    width: 48%;
    float: left;
    padding: 30px 50px;
    margin-right: 2%;
    BOX-SIZING: BORDER-BOX;
    margin-bottom: 30PX;
}
.box-div h3
{
	font-size: 25px;
    font-weight: 600;
    position: relative;;
}
.box-div h3:after
{
	content: "";
    width: 50px;
    position: absolute;
    left: 0px;
    border: 1px solid black;
    top: 38px;
}
.box-div h4
{
	margin-bottom: 10px;
}
.Squarearea p
{
	padding-left: 20px;
	position: relative;
}
.Squarearea p:before
{
	content: "";
    width: 10px;
    position: absolute;
    left: 0px;
    border: 1px solid black;
    top: 2px;
    height: 10px;
}